import { useState, useEffect, useRef } from "react";
import { DailyExpContainer, ExpContent, ExpTitle, Header, LoginContainer, MiddleContainer, TitleBox } from "../styledComponent/style";
import { BiChevronLeft, BiChevronRight } from "react-icons/bi";

import moment from "moment";

function DailyExpenditure() {
    const [year, changeYear] = useState(2022);
    const [month, changeMonth] = useState(6);
    const [day, changeDay] = useState(1);
    let endOfMonth = moment().year(year).month(month).endOf("month").format("DD");
    const modalRef = useRef();

    const increaseDate = () => {
        // day + 1     
        if (month + 1 > 12) {
            changeYear(year + 1);
            changeMonth(1);
            changeDay(1);
        } else {
            // month  
            changeMonth(month + 1);
            changeDay(1);
        }
    };

    const decreaseDate = () => {
        // day - 1 0   
        if (month - 1 === 0) {
            // year  !
            changeYear(year - 1);
            changeMonth(12);
            endOfMonth = moment().year(year).month(month).endOf("month").format("DD");
            changeDay(endOfMonth);
        } else {
            // month   .
            changeMonth(month - 1);
            endOfMonth = moment().year(year).month(month).endOf("month").format("DD");
            changeDay(endOfMonth);
        }
    };

    return (
        <LoginContainer>
            <Header>
                <TitleBox>
                    <BiChevronLeft
                        size="2rem"
                        cursor="pointer"
                        onClick={() => {
                            if (day <= 1) decreaseDate();
                            else changeDay(day - 1);
                        }}
                    />
                </TitleBox>
                <h2 style={{ fontSize: "1.25rem" }}>
                    {year}-{month}-{day}
                </h2>
                <TitleBox>
                    <BiChevronRight
                        size="2rem"
                        cursor="pointer"
                        onClick={() => {
                            if (day >= endOfMonth) increaseDate();
                            else changeDay(day + 1);
                        }}
                    />
                </TitleBox>
            </Header>
            <DailyExpContainer>
                <ExpTitle></ExpTitle>
                <ExpTitle></ExpTitle>
                <ExpTitle></ExpTitle>
            </DailyExpContainer>
            <DailyExpContainer>
                <ExpContent></ExpContent>
                <ExpContent></ExpContent>
                <ExpContent>-8,000</ExpContent>
            </DailyExpContainer>
            <DailyExpContainer>
                <ExpContent></ExpContent>
                <ExpContent> </ExpContent>
                <ExpContent>-2,500</ExpContent>
            </DailyExpContainer>
            <DailyExpContainer>
                <ExpContent></ExpContent>
                <ExpContent> </ExpContent>
                <ExpContent>-1,500</ExpContent>
            </DailyExpContainer>
        </LoginContainer>
    );
}

export default DailyExpenditure;
